import React, {useState} from "react";
import {Button, Checkbox} from "antd";
import {requestAPI} from "./API";

export const Request = () => {

    const [checked, setChecked] = useState<boolean>(false)
    const [res, setRes] = useState('')
    const [error, setError] = useState('')


    const postAPI = () => {
        requestAPI.sendPost(checked)
            .then(res => {
                setRes(res.data.yourBody.success)
                setError(res.data.errorText)
                console.log({...res})
            })
            .catch(err => {
                setRes('')
                console.log({...err})

                setError(err.response.data.errorText)
            })
    }

    return (
        <>
            <Button onClick={postAPI}>
                asd
            </Button>
            <Checkbox value={checked} onChange={() => setChecked(!checked)}/>
            <div>
                <span>Response :</span>
                <span>{JSON.stringify(res)}</span>
            </div>
            <div>
                <span>Error :</span>
                <span>{error}</span>
            </div>

        </>
    )
}